import { motion } from 'framer-motion';
import { FaGithubSquare } from "react-icons/fa";



const ProjectSingle = ({ title, category, image, url }) => {
	return (
		<motion.div
			initial={{ opacity: 0 }}
			animate={{ opacity: 1, delay: 1 }}
			transition={{
				ease: 'easeInOut',
				duration: 0.7,
				delay: 0.15,
			}}
		>

			<div className="rounded-xl shadow-lg hover:shadow-xl cursor-pointer mb-10 sm:mb-0 bg-ternary-light dark:bg-ternary-dark">
				<div>
					<img
						src={image}
						className="rounded-t-xl border-none"
						alt="Single Project"
					/>
				</div>
				<div className="text-center px-4 py-6">
					<p className="font-general-medium text-lg md:text-xl text-ternary-dark dark:text-ternary-light mb-2">
						{title}
					</p>
					<span className="text-lg text-ternary-dark dark:text-ternary-light mb-2">
						{category}
					</span>
					<div className="text-center text-ternary-dark dark:text-ternary-light">
						<a href={url}>
							<i className='text-xl sm:text-2xl md:text-3xl'><FaGithubSquare /></i>
						</a>
					</div>
				</div>

			</div>
		</motion.div>
	);
};

export default ProjectSingle;
